import { Dispatch, SetStateAction, useEffect, useState } from "react";
import {
  Badge,
  Box,
  Button,
  Card,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControl,
  IconButton,
  InputLabel,
  MenuItem,
  Select,
  SelectChangeEvent,
  Typography,
} from "@mui/material";
import { Edit as EditIcon } from "@mui/icons-material";

const deviceTypes = ["MET", "NCU"];
const deviceIds = ["MET1", "MET2", "MET3", "MET4", "MET5", "MET6", "MET7"];
const points = [
  "Pressure",
  "Wind Speed",
  "Temperature",
  "Humidity",
  "Rainfall",
];

type BasicSelectProps = {
  options: string[];
  label: string;
  setValue?: Dispatch<SetStateAction<string>>;
  value?: string;
};

export function BasicSelect({
  options,
  label,
  setValue,
  value,
}: BasicSelectProps): JSX.Element {
  const onChange = (e: SelectChangeEvent): void => {
    if (setValue) {
      setValue(e.target.value);
    }
  };
  return (
    <Box style={{ minWidth: 130 }}>
      <FormControl fullWidth>
        <InputLabel id="demo-simple-select-label">{label}</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={value}
          label={label}
          onChange={onChange}
        >
          {options.map((option) => (
            <MenuItem key={option} value={option}>
              {option}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </Box>
  );
}

interface PointContentProps {
  value: string;
  point: string | null;
  unit?: string;
}
function PointContent({ value, point, unit }: PointContentProps): JSX.Element {
  return (
    <Card
      style={{
        width: 250,
        display: "flex",
        padding: 15,
        alignItems: "center",
        justifyContent: "space-between",
      }}
    >
      <Typography variant="body1">{point}</Typography>
      <Box style={{display: 'flex'}}>
        <Box mr={1} style={{fontWeight: "bold"}} >{value}</Box>
        <Typography variant="body1">{unit}</Typography>
      </Box>
    </Card>
  );
}
interface PointDialogProps {
  dialog: boolean;
  setDialog: Dispatch<SetStateAction<boolean>>;
  setPoint: Dispatch<SetStateAction<string>>;
  setUnit: Dispatch<SetStateAction<string>>;
}

function PointDialog(p: PointDialogProps): JSX.Element {
  const [point, setPoint] = useState("");
  const onClose = (): void => {
    p.setDialog(false);
  };

  const handleSave = (): void => {
    p.setPoint(point);
    p.setUnit("hPa")
    p.setDialog(false);
  };
  return (
    <Dialog onClose={onClose} open={p.dialog}>
      <DialogTitle>Configure Point</DialogTitle>
      <DialogContent
        style={{
          padding: 30,
          display: "flex",
        }}
      >
        <Box mr={2}>
          <BasicSelect options={deviceTypes} label="Device Type" />
        </Box>
        <Box mr={2}>
          <BasicSelect options={deviceIds} label="Device ID" />
        </Box>
        <BasicSelect options={points} label="Point" setValue={setPoint} />
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>Cancel</Button>
        <Button onClick={handleSave}>Save</Button>
      </DialogActions>
    </Dialog>
  );
}
export default function SchemaPoint(): JSX.Element {
  const [point, setPoint] = useState("Irradiance");
  const [value, setValue] = useState("--");
  const [unit, setUnit] = useState("W/m²");

  const [editIcon, setEditIcon] = useState(false);
  const [dialog, setDialog] = useState(false);

  useEffect(() => {
    const interval = setInterval(() => {
      setValue((Math.random() * 101).toFixed(2));
    }, 1000);
    return () => clearInterval(interval);
  }, []);

  return (
    <Box
      onMouseEnter={(): void => setEditIcon(true)}
      onMouseLeave={(): void => setEditIcon(false)}
    >
      {editIcon ? (
        <Badge
          badgeContent={
            <IconButton onClick={(): void => setDialog(true)}>
              <EditIcon style={{ color: "white" }} fontSize="small" />
            </IconButton>
          }
          color="primary"
        >
          <PointContent unit={unit} point={point} value={value} />
        </Badge>
      ) : (
        <PointContent unit={unit} point={point} value={value} />
      )}
      <PointDialog
        setUnit={setUnit}
        setPoint={setPoint} dialog={dialog} setDialog={setDialog} />
    </Box>
  );
}
